@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
	margin: 0;
	padding: 0;
    list-style: none;
	font-family: "Poppins", serif;
	font-weight: 300;
	font-style: thin;
	color: white;
}

a {
	text-decoration: none; /* Remove sublinhado */
	color: inherit; /* Faz o link herdar a cor do elemento pai */
  }
  
  a:visited {
	color: inherit; /* Remove a cor padrão dos links visitados */
  }
  
  a:hover,
  a:focus {
	text-decoration: none; /* Garante que não reapareça o sublinhado no hover */
	outline: none; /* Remove contorno ao focar no link */
  }